import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";
import moment from "moment";
import NumberFormat from "../../../App/NumberFormat";

import notification from "../../../../components/notification";
import {
  Empty,
  Collapse,
  Select,
  DatePicker,
  Row,
  Col,
  Divider,
  Input
} from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/report/debitMemoReport/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  Box,
  customCollapseStyle,
  TransNo,
  StatusBadge,
  StatusTag
} from "../../../App/app.style";
import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import ExcelWrapper from "../../../App/ExcelWrapper";
import PDFWrapper from "../../../App/PDFWrapper";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

const Panel = Collapse.Panel;
const Option = Select.Option;
const Search = Input.Search;
const { RangePicker } = DatePicker;

const redirectTo = "/dashboard/report-menus";
const redirectPO = "/dashboard/purchases/purchase-orders";
const redirectPT = "/dashboard/purchases/purchase-returns";

class DebitMemoReport extends Component {
  state = {
    filter: {
      id_supplier: null,
      id_location: null,
      transaction_type: null,
      status: null,
      id_cash_bank: null,
      start_date: moment()
        .startOf("month")
        .format("YYYY-MM-DD HH:mm:ss"),
      end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
      sort_by: "transaction_date",
      sort_type: "asc",
      pagination: null
    },
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getDebitMemoReports } = this.props;
    getDebitMemoReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.debitMemoReport.error) {
      notification("error", this.props.debitMemoReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.debitMemoReport.success) {
      notification("success", this.props.debitMemoReport.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    const { getDebitMemoReports } = this.props;
    let filter = this.filterValue();

    getDebitMemoReports(1, value, filter, this.state.filter);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getDebitMemoReports } = this.props;
    this.setState({
      filter: {
        id_supplier: null,
        id_location: null,
        transaction_type: null,
        status: null,
        id_cash_bank: null,
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        sort_by: "transaction_date",
        sort_type: "asc",
        pagination: null
      },
      search: null
    });
    getDebitMemoReports();
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else if (name === "id_location") {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value,
          id_cash_bank: null,
          id_supplier: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.status) {
      string += "status=" + filter.status;
    }
    if (filter.id_location) {
      string
        ? (string += "&id_location=" + filter.id_location)
        : (string += "id_location=" + filter.id_location);
    }
    if (filter.transaction_type) {
      string
        ? (string += "&transaction_type=" + filter.transaction_type)
        : (string += "transaction_type=" + filter.transaction_type);
    }
    if (filter.id_cash_bank) {
      string
        ? (string += "&id_cash_bank=" + filter.id_cash_bank)
        : (string += "id_cash_bank=" + filter.id_cash_bank);
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }
    if (filter.id_supplier) {
      string
        ? (string += "&id_supplier=" + filter.id_supplier)
        : (string += "id_supplier=" + filter.id_supplier);
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }

    string
      ? (string +=
          "&sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type)
      : (string +=
          "sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type);

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getDebitMemoReports } = this.props;
    //const { current_page } = this.props.debitMemoReport;
    let filter = this.filterValue();

    this.setState({ search: null });
    getDebitMemoReports(1, null, filter, this.state.filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getDebitMemoReports } = this.props;
    this.setState({
      filter: {
        id_supplier: null,
        id_location: null,
        transaction_type: null,
        status: null,
        id_cash_bank: null,
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        sort_by: "transaction_date",
        sort_type: "asc",
        pagination: null
      }
    });
    getDebitMemoReports();
  };

  // // Handle pagination
  // handlePageChange = page => {
  //   const { getDebitMemoReports } = this.props;
  //   const { filter } = this.props.debitMemoReport;
  //   getDebitMemoReports(page, null, filter, this.state.filter);
  // };

  render() {
    const columns = [
      {
        title: getLanguage({ id: "transactionNo" }),
        dataIndex: "debit_memo_no",
        rowKey: "debit_memo_no",
        width: "14%",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 7,
        render: (text, row) => {
          if (row.debit_memo_no === "Total") {
            return {
              children: <strong style={{ marginLeft: "50%" }}>{text}</strong>,
              props: {
                colSpan: 7
              }
            };
          }

          let className;
          switch (row.status) {
            case 4:
              className = "new";
              break;
            case 1:
              className = "ordered";
              break;
            case 2:
              className = "closed";
              break;
            case 3:
              className = "cancelled";
              break;
            default:
              className = "new";
              break;
          }

          return (
            <div style={{ display: "flex", height: "20px" }}>
              <StatusBadge className={className} />
              <TransNo>{text}</TransNo>
            </div>
          );
          // return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({ id: "paymentTransaction" }),
        dataIndex: "transaction_no",
        rowKey: "transaction_no",
        width: "14%",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0,
        render: (text, row) => {
          if (row.debit_memo_no === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          switch (row.transaction_type) {
            case 6:
              return (
                <Link to={`${redirectPO}/update/${row.id_transaction}`}>
                  {text}
                </Link>
              );
            case 7:
              return (
                <Link to={`${redirectPT}/update/${row.id_transaction}`}>
                  {text}
                </Link>
              );
            default:
              return "-";
          }
        }
      },
      {
        title: getLanguage({ id: "transactionDate" }),
        dataIndex: "transaction_date",
        rowKey: "transaction_date",
        width: "11%",
        date: true,
        dateFormat: "DD MMMM YYYY HH:mm:ss",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0,
        render: (text, row) => {
          if (row.debit_memo_no === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>;
        }
      },
      {
        title: getLanguage({ id: "sidebar.supplier" }),
        dataIndex: "supplier_name",
        rowKey: "supplier_name",
        width: "12%",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0,
        render: (text, row) => {
          if (row.debit_memo_no === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return text;
        }
      },
      {
        title: getLanguage({ id: "sidebar.currency" }),
        dataIndex: "currency.currency_code",
        rowKey: "currency.currency_code",
        width: "5%",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0,
        render: (text, row) => {
          if (row.debit_memo_no === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return text;
        }
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "string_status",
        rowKey: "string_status",
        width: "9%",
        align: "center",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0,
        render: (text, row) => {
          if (row.debit_memo_no === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          let className;
          switch (row.status) {
            case 4:
              className = "new";
              break;
            case 1:
              className = "ordered";
              break;
            case 2:
              className = "closed";
              break;
            case 3:
              className = "cancelled";
              break;
            default:
              className = "new";
              break;
          }

          return <StatusTag className={className}>{text}</StatusTag>;
        }
      },
      {
        title: getLanguage({ id: "totalAmount" }),
        dataIndex: "currency_amount",
        rowKey: "currency_amount",
        width: "15%",
        align: "right",
        parse: true,
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0,
        render: (text, row) => {
          if (row.debit_memo_no === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return <NumberFormat value={parseFloat(text)} />;
        }
      },
      {
        title: getLanguage({ id: "baseAmountCurr" }),
        dataIndex: "amount",
        rowKey: "amount",
        width: "15%",
        align: "right",
        parse: true,
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 1,
        render: (text, row) => {
          if (row.debit_memo_no === "Total") {
            return (
              <strong>
                <NumberFormat value={parseFloat(text)} />
              </strong>
            );
          }

          return <NumberFormat value={parseFloat(text)} />;
        }
      }
    ];

    // Data setup
    const {
      debit_memo_reports,
      locations,
      suppliers,
      cash_banks,
      filter,
      filter_select
    } = this.props.debitMemoReport;
    //console.log(this.props.debitMemoReport.base_currency); //console.log(this.state);
    const dataSource = [];

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    let total = 0;
    debit_memo_reports.map((data, key) => {
      switch (data.status) {
        case 4:
          data.string_status = getLanguage({ id: "new" });
          break;
        case 1:
          data.string_status = getLanguage({ id: "open" });
          break;
        case 2:
          data.string_status = getLanguage({ id: "closed" });
          break;
        case 3:
          data.string_status = getLanguage({ id: "cancelled" });
          break;
        default:
          data.string_status = getLanguage({ id: "new" });
          break;
      }

      total += parseFloat(data.amount);
      dataSource.push({
        ...data,
        key
      });
    });

    dataSource.push({
      key: debit_memo_reports.length,
      debit_memo_no: "Total",
      amount: total
    });

    const { loading, loadingForeign } = this.props.debitMemoReport;

    const fileName = getLanguage({ id: "debitMemoReports.pageHeader" })
    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="debitMemoReports.pageHeader" />}
            marginBottom={true}
          >
            <PrintWrapper
              componentRef={ref => (this.componentRef = ref)}
              title={<IntlMessages id="debitMemoReports.pageHeader" />}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
              cash_banks={cash_banks}
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
              onBeforePrint={() => (document.title = getFileName(fileName, null, false))}
              onAfterPrint={() => document.title = siteConfig.documentTitle}
              pageStyle={
                "@page { size: landscape; }" +
                "body {" +
                "-webkit-print-color-adjust: exact;" +
                "} "
              }
            />

            <PDFWrapper
              cash_banks={cash_banks}
              columns={columns}
              data={dataSource}
              fileName={fileName}
              filter={filter_select}
              landscape={true}
              loading={loading}
              locations={locations}
              scale={0.5}
              title="debitMemoReports.pageHeader"
            />

            <ExcelWrapper
              title="debitMemoReports.pageHeader"
              fileName={fileName}
              loading={loading}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
              cash_banks={cash_banks}
            />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={
                getLanguage({ id: "transactionNo" }) +
                ", " +
                getLanguage({ id: "supplierName" })
              }
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.searchFilter" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={<IntlMessages id="select.location" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>

                  <Col span={12}>
                    <label>
                      <IntlMessages id="suppliers.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_supplier"
                      )}
                      value={
                        this.state.filter.id_supplier
                          ? this.state.filter.id_supplier
                          : []
                      }
                      style={{ width: "100%" }}
                      placeholder={
                        this.state.filter.id_location ? (
                          <IntlMessages id="select.supplier" />
                        ) : (
                            <IntlMessages id="noLocation" />
                          )
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      notFoundContent={
                        this.state.filter.id_location ? (
                          <Empty description={<IntlMessages id="noData" />} />
                        ) : (
                          <Empty
                            description={<IntlMessages id="noLocation" />}
                          />
                        )
                      }
                      //showArrow={false}
                    >
                      {suppliers.map((data, key) => {
                        if (
                          this.state.filter.id_location === data.id_location
                        ) {
                          return (
                            <Option key={key} value={data.id_supplier}>
                              {`${data.supplier_code} (${data.supplier_name})`}
                            </Option>
                          );
                        }
                      })}
                    </Select>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="cashBanks.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_cash_bank"
                      )}
                      value={
                        this.state.filter.id_cash_bank
                          ? this.state.filter.id_cash_bank
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        this.state.filter.id_location ? (
                          <IntlMessages id="select.cashBank" />
                        ) : (
                            <IntlMessages id="noLocation" />
                          )
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      // loading={this.props.location.loadingFilterSites}
                      allowClear={true}
                      notFoundContent={
                        this.state.filter.id_location ? (
                          <Empty description={<IntlMessages id="noData" />} />
                        ) : (
                          <Empty
                            description={<IntlMessages id="noLocation" />}
                          />
                        )
                      }
                      //showArrow={false}
                    >
                      {cash_banks.map((data, key) => {
                        if (
                          this.state.filter.id_location === data.id_location
                        ) {
                          return (
                            <Option key={key} value={data.id_cash_bank}>
                              {data.bank_account_number === null
                                ? data.cash_bank_code
                                : data.cash_bank_code +
                                  " (" +
                                  data.bank_account_number +
                                  ")"}
                            </Option>
                          );
                        }
                      })}
                    </Select>
                  </Col>

                  <Col span={12}>
                    <label>
                      <IntlMessages id="salesOrders.filter.date" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={false}
                      style={{ width: "100%" }}
                      value={
                        this.state.filter.start_date &&
                        this.state.filter.end_date
                          ? [
                              moment(this.state.filter.start_date),
                              moment(this.state.filter.end_date)
                            ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <Row justify="start" gutter={1} type="flex">
                      <Col span={12}>
                        <label>
                          <IntlMessages id="transactionType" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "transaction_type"
                          )}
                          value={
                            this.state.filter.transaction_type
                              ? this.state.filter.transaction_type
                              : null
                          }
                          style={{ width: "95%" }}
                          // placeholder={
                          //   <IntlMessages id="pettyCashTypes.filter.select.transactionType" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={6} value={6}>
                            <IntlMessages id="supplierDeposit" />
                          </Option>
                          <Option key={7} value={7}>
                            <IntlMessages id="debitMemo" />
                          </Option>
                        </Select>
                      </Col>
                      <Col span={12}>
                        <label>{getLanguage({ id: "status" })}</label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "status"
                          )}
                          value={
                            this.state.filter.status
                              ? this.state.filter.status
                              : undefined
                          }
                          style={{ width: "90%" }}
                          placeholder={getLanguage({ id: "status" })}
                          optionFilterProp="children"
                          filterOption={true}
                          allowClear={true}
                          //showArrow={false}
                        >
                          <Option value={4}>
                            {getLanguage({ id: "new" })}
                          </Option>
                          <Option value={1}>
                            {getLanguage({ id: "open" })}
                          </Option>
                          <Option value={2}>
                            {getLanguage({ id: "closed" })}
                          </Option>
                          <Option value={3}>
                            {getLanguage({ id: "cancelled" })}
                          </Option>
                        </Select>
                      </Col>
                    </Row>
                  </Col>
                  <Col span={12}>
                    <Row justify="start" gutter={1} type="flex">
                      <Col span={12}>
                        <label>
                          <IntlMessages id="stockValuationReports.sortBy" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_by"
                          )}
                          value={
                            this.state.filter.sort_by
                              ? this.state.filter.sort_by
                              : "transaction_date"
                          }
                          style={{ width: "95%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value="transaction_date">
                            <IntlMessages id="debitMemoReports.filter.date" />
                          </Option>
                          <Option key={4} value="debit_memo_no">
                            <IntlMessages id="debitMemos.no" />
                          </Option>
                          <Option key={4} value="supplier_name">
                            <IntlMessages id="supplierName" />
                          </Option>
                          <Option key={5} value="amount">
                            <IntlMessages id="totalAmount" />
                          </Option>
                        </Select>
                      </Col>
                      <Col span={12}>
                        <label>
                          <IntlMessages id="sortType" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_type"
                          )}
                          value={
                            this.state.filter.sort_type
                              ? this.state.filter.sort_type
                              : "asc"
                          }
                          style={{ width: "100%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value="asc">
                            <IntlMessages id="ascending" />
                          </Option>
                          <Option key={2} value="desc">
                            <IntlMessages id="descending" />
                          </Option>
                        </Select>
                      </Col>
                    </Row>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "24px" }}
                >
                  <Col
                    span={8}
                    style={{
                      marginTop: "24px"
                    }}
                  >
                    <Button
                      size="small"
                      type="primary"
                      style={{ marginRight: "10px" }}
                      onClick={this.handleFilter}
                    >
                      <IntlMessages id="filter.only" />
                    </Button>
                    <Button onClick={this.handleResetFilter} size="small">
                      <IntlMessages id="filter.reset" />
                    </Button>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {!filter ? (
              <Empty description={<IntlMessages id="clicksearchFilter" />} />
            ) : debit_memo_reports.length === 0 &&
              !this.props.debitMemoReport.loading ? (
              <Empty
                description={
                  <IntlMessages id="debitMemoReports.table.noData" />
                }
              />
            ) : (
              <div className="isoInvoiceTable">
                <Divider orientation="left" style={{ fontSize: 14 }}>
                  <IntlMessages id="details" />
                </Divider>
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    columns={columns}
                    loading={loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={false}
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    debitMemoReport: state.debitMemoReport,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(DebitMemoReport);
